import React, { useEffect, useState } from 'react'

// Import Table 
import Table from './Table'

// Third party Imports 
import { Row, Col, Label, Input, Button } from 'reactstrap'
import { Filter } from 'react-feather'
import Select from 'react-select'
import { useDispatch, useSelector } from 'react-redux'

// Import Styles
import { selectThemeColors } from '@utils'

// Import Appcollapse from Custom Compponents
import AppCollapse from '@components/app-collapse'

// Import dispatch actions from store
import { searchCricketUsers } from '../../store/actions'

// Import Breadcrumbs 
import BreadCrumbs from '@components/breadcrumbs'

function Header() {

    const typeOptions = [
        { value: '1', label: 'ODI' },
        { value: '2', label: 'T10' },
        { value: '3', label: 'T20' },
        { value: '4', label: 'T20I' },
        { value: '5', label: 'TEST' },
        { value: '6', label: 'MIXED' },
        { value: '7', label: 'WOMANT20' }

    ]

    const statusOptions = [
        { value: '1', label: 'Live' },
        { value: '2', label: 'Upcoming' },
        { value: '3', label: 'Completed' },
        { value: '4', label: 'Cancelled' }
       
    ]

    const dispatch = useDispatch()
    const store = useSelector((state) => state.series)
    const [searchName, setSearchName] = useState('')
    const [searchType, setSearchType] = useState('')
    const [searchStatus, setSearchStatus] = useState('')
    const [currentPage, setCurrentPage] = useState(true)
    const dataPerPage = store.dataPerPage
    const statusCode = searchStatus === 'Live' ? 1 : searchStatus === 'Upcoming' ? 2 : searchStatus === 'Completed' ? 3 : searchStatus === 'Cancelled' ? 4 : ''

    useEffect(() => {
        dispatch(searchCricketUsers(searchName, searchType, statusCode, dataPerPage))
    }, [])

    const handleSubmit = () => {
        setCurrentPage(!currentPage)
        dispatch(searchCricketUsers(1, searchName, searchType, statusCode, dataPerPage))
    }

    const handleKeyPress = (e) => {
        if (e.keyCode === 13) {
            setCurrentPage(!currentPage)
            dispatch(searchCricketUsers(currentPage, searchName, searchType, statusCode, dataPerPage))
        }
    }

    const data = [
        {
            title: <div><Filter size={18} /> Filter</div>,
            content: (
                <>
                    <Row xl={12}>
                        <Col
                            xl="12"
                            className="d-flex align-items-sm-center justify-content  flex-lg-nowrap flex-wrap flex-sm-row flex-column pr-lg-1 p-0 mt-lg-0 mt-1"
                        >
                            <div className="align-items-center mb-sm-0 mb-1 mr-1 ml-1">
                                <Label>
                                    Series Name:
                                </Label>
                                <Input
                                    id="search-invoice"
                                    className="w-100"
                                    type="text"
                                    value={searchName}
                                    placeholder='Enter series name'
                                    onChange={(e) => setSearchName(e.target.value)}
                                    onKeyDown={(e) => handleKeyPress(e)}
                                />
                            </div>
                        </Col>
                    </Row>
                    <Row>
                        <Col className='mb-1 mt-1' md='6' sm='12'>
                            <Label>Series Type:</Label>
                            <Select
                                menuPortalTarget={document.querySelector('body')}
                                onChange={(e) => setSearchType(e?.label)}
                                theme={selectThemeColors}
                                className='react-select'
                                classNamePrefix='select'
                                options={typeOptions}
                                placeholder='search series type...'
                                isClearable={true}
                                onKeyDown={(e) => handleKeyPress(e)}
                            />
                        </Col>
                        <Col className='mb-1 mt-1' md='6' sm='12'>
                            <Label>Series Status:</Label>
                            <Select
                                menuPortalTarget={document.querySelector('body')}
                                onChange={(e) => setSearchStatus(e?.label)}
                                theme={selectThemeColors}
                                className='react-select'
                                classNamePrefix='select'
                                // defaultValue = {statusOptions[0]}
                                options={statusOptions}
                                placeholder='search series status...'
                                isClearable={true}
                                onKeyDown={(e) => handleKeyPress(e)}
                            />
                        </Col>
                    </Row>
                    <Button color='primary' className='pl-1' onClick={handleSubmit} >Submit</Button>
                </>
            )
        }
    ]

    return (
        <div>
            <BreadCrumbs breadCrumbTitle='Cricket Series' breadCrumbActive='List' />
            <AppCollapse data={data} type='border' accordion className='mb-1' />
            <Table name={searchName} status={searchStatus} type={searchType} page={currentPage} />
        </div>
    )
}

export default Header